.cs-navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  padding: 18px 0;
  transition: background 0.4s ease, box-shadow 0.4s ease, backdrop-filter 0.4s ease, padding 0.4s ease, border-color 0.4s ease;
  background: rgba(9, 14, 28, 0.55);
  border-bottom: 1px solid rgba(0, 170, 255, 0.05);
  backdrop-filter: blur(18px);
}

.cs-navbar--scrolled {
  background: rgba(8, 12, 24, 0.95);
  backdrop-filter: blur(14px);
  box-shadow: 0 18px 38px rgba(4, 8, 24, 0.45);
  padding: 12px 0;
  border-color: rgba(0, 170, 255, 0.12);
}

.cs-navbar__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
}

.cs-navbar__brand {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  color: var(--cs-text);
}

.cs-navbar__brand img {
  height: 52px;
  width: auto;
  display: block;
  filter: drop-shadow(0 12px 24px rgba(0, 61, 150, 0.35));
  transition: transform 0.4s ease;
}

.cs-navbar__brand:hover img {
  transform: translateY(-2px) scale(1.02);
}

.cs-navbar__divider {
  width: 1px;
  height: 36px;
  background: linear-gradient(180deg, rgba(0, 170, 255, 0.6), rgba(0, 61, 150, 0.2));
  border-radius: 999px;
}

.cs-navbar__links {
  display: flex;
  align-items: center;
  gap: 28px;
  margin-left: auto;
}

.cs-navbar__link {
  position: relative;
  font-size: 15px;
  color: var(--cs-muted);
  padding: 4px 0;
  font-weight: 500;
}

.cs-navbar__link::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -8px;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--cs-blue), rgba(0, 61, 150, 0));
  transition: width 0.3s ease, left 0.3s ease;
}

.cs-navbar__link:hover {
  color: var(--cs-text);
}

.cs-navbar__link:focus-visible {
  outline: none;
  color: var(--cs-text);
  text-shadow: 0 0 12px rgba(0, 170, 255, 0.55);
}

.cs-navbar__link:hover::after {
  width: 100%;
  left: 0;
}

.cs-navbar__cta {
  margin-left: 16px;
  padding-inline: 24px;
  box-shadow: 0 16px 40px rgba(255, 145, 77, 0.35);
}

.cs-navbar__menu {
  display: none;
  background: none;
  border: none;
  color: var(--cs-text);
  font-size: 28px;
}

.cs-navbar__drawer {
  position: fixed;
  inset: 0;
  background: rgba(4, 8, 18, 0.94);
  backdrop-filter: blur(16px);
  transform: translateX(100%);
  transition: transform 0.4s ease;
  z-index: 1200;
  display: flex;
  flex-direction: column;
  padding: 28px;
}

.cs-navbar__drawer.is-open {
  transform: translateX(0);
}

.cs-navbar__drawer-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  margin-bottom: 32px;
}

.cs-navbar__drawer-header button {
  background: none;
  border: none;
  color: var(--cs-text);
  font-size: 28px;
}

.cs-navbar__drawer-links {
  display: grid;
  gap: 18px;
  font-size: 20px;
}

.cs-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px 26px;
  border-radius: 999px;
  font-weight: 600;
  letter-spacing: 0.02em;
  transition: transform 0.2s ease, box-shadow 0.3s ease, background 0.2s ease;
  border: 1px solid transparent;
  position: relative;
  overflow: hidden;
}

.cs-button::after {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.25), transparent 60%);
  opacity: 0;
  transition: opacity 0.4s ease;
  border-radius: inherit;
}

.cs-button:hover::after {
  opacity: 1;
  animation: cs-button-ripple 1.2s ease;
}

.cs-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 28px 60px rgba(0, 61, 150, 0.28);
}

.cs-button:active {
  transform: translateY(0);
  box-shadow: 0 16px 28px rgba(0, 61, 150, 0.22);
}

.cs-button--primary {
  background: linear-gradient(140deg, #003d96, #00aaff);
  color: white;
  box-shadow: 0 20px 42px rgba(0, 106, 214, 0.45);
}

.cs-button--primary:hover {
  background: linear-gradient(140deg, #0050d6, #1dc0ff);
  box-shadow: 0 26px 55px rgba(0, 140, 255, 0.45);
}

.cs-button--secondary {
  background: linear-gradient(140deg, #ff914d, #ffb46a);
  color: white;
  box-shadow: 0 18px 38px rgba(255, 145, 77, 0.45);
}

.cs-button--secondary:hover {
  background: linear-gradient(140deg, #ff9f62, #ffc27f);
  box-shadow: 0 24px 46px rgba(255, 145, 77, 0.48);
}

.cs-button--ghost {
  background: transparent;
  color: var(--cs-text);
  border-color: rgba(234, 240, 255, 0.2);
  box-shadow: inset 0 0 0 0 rgba(234, 240, 255, 0.2);
}

.cs-button--ghost:hover {
  box-shadow: inset 0 0 0 1px rgba(234, 240, 255, 0.45), 0 18px 32px rgba(234, 240, 255, 0.12);
}

.cs-particles {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
}

.cs-hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 140px 0 100px;
  overflow: hidden;
}

.cs-backdrop {
  position: absolute;
  inset: 0;
  pointer-events: none;
  overflow: hidden;
  z-index: -2;
}

.cs-backdrop__shape {
  position: absolute;
  border-radius: 50%;
  opacity: 0.9;
  mix-blend-mode: screen;
}

.cs-hero__background {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  transform: scale(1.1);
  filter: brightness(0.5);
  z-index: -3;
}

.cs-hero__overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(120deg, rgba(11, 16, 32, 0.92), rgba(0, 61, 150, 0.55));
  z-index: -1;
}

.cs-hero__content {
  position: relative;
  display: grid;
  grid-template-columns: minmax(0, 1fr) minmax(240px, 320px);
  gap: 56px;
  align-items: center;
  margin: 0 auto;
}

.cs-hero__col {
  max-width: 640px;
  justify-self: center;
}

.cs-hero__title {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin: 18px 0 20px;
}

.cs-hero__type {
  display: inline-block;
  background: linear-gradient(120deg, #ffffff, rgba(234, 240, 255, 0.8));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.cs-hero__col h1 {
  font-family: 'Poppins', sans-serif;
  font-size: clamp(42px, 6vw, 72px);
  line-height: 1.08;
  margin: 18px 0 20px;
}

.cs-hero__eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.36em;
  font-size: 13px;
  color: var(--cs-muted);
  display: inline-block;
}

.cs-hero__lead {
  max-width: 560px;
  color: var(--cs-muted);
  font-size: 18px;
}

.cs-hero__actions {
  display: flex;
  gap: 16px;
  margin: 28px 0 32px;
}


.cs-hero__search {
  background: rgba(12, 19, 35, 0.72);
  border: 1px solid rgba(159, 178, 217, 0.12);
  border-radius: var(--cs-radius);
  padding: 20px 24px;
  display: grid;
  gap: 16px;
  box-shadow: var(--cs-shadow-soft);
  max-width: 460px;
}

.cs-hero__search-input {
  display: flex;
  align-items: center;
  gap: 14px;
  padding: 12px 16px;
  border-radius: 999px;
  background: rgba(234, 240, 255, 0.06);
  border: 1px solid rgba(159, 178, 217, 0.18);
  box-shadow: inset 0 0 0 0 rgba(0, 61, 150, 0.2);
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.cs-hero__search-input svg {
  font-size: 20px;
  color: rgba(234, 240, 255, 0.65);
  transition: transform 0.3s ease;
}

.cs-hero__search-input input {
  flex: 1;
  border: none;
  background: transparent;
  color: var(--cs-text);
  font-size: 15px;
  outline: none;
  width: 100%;
}

.cs-hero__search-input input::placeholder {
  color: rgba(234, 240, 255, 0.55);
}

.cs-hero__search-input:focus-within {
  border-color: rgba(0, 61, 150, 0.6);
  box-shadow: 0 18px 36px rgba(0, 61, 150, 0.2);
}

.cs-hero__search-input:focus-within svg {
  transform: translateX(2px);
  color: rgba(0, 170, 255, 0.9);
}

.cs-hero__search-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.cs-hero__search-tags button {
  background: linear-gradient(120deg, rgba(0, 61, 150, 0.28), rgba(0, 170, 255, 0.24));
  border: 1px solid rgba(0, 170, 255, 0.35);
  color: var(--cs-text);
  border-radius: 999px;
  padding: 8px 18px;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.cs-hero__search-tags button:hover {
  transform: translateY(-2px);
  box-shadow: 0 18px 30px rgba(0, 170, 255, 0.25);
}

.cs-hero__stats {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 18px;
  background: rgba(15, 22, 39, 0.78);
  border-radius: var(--cs-radius);
  border: 1px solid rgba(0, 170, 255, 0.16);
  padding: 28px;
  box-shadow: 0 26px 60px rgba(0, 61, 150, 0.22);
  justify-self: center;
  width: min(100%, 360px);
  backdrop-filter: blur(12px);
}

.cs-hero__stat {
  background: linear-gradient(140deg, rgba(0, 61, 150, 0.35), rgba(0, 170, 255, 0.12));
  border-radius: var(--cs-radius-sm);
  border: 1px solid rgba(0, 170, 255, 0.25);
  padding: 16px 18px;
  display: grid;
  gap: 8px;
  box-shadow: inset 0 0 0 0 rgba(0, 170, 255, 0.16);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}

.cs-hero__stat-icon {
  width: 32px;
  height: 32px;
  border-radius: 12px;
  display: grid;
  place-items: center;
  background: rgba(0, 170, 255, 0.16);
  color: rgba(0, 170, 255, 0.9);
}

.cs-hero__stat span {
  font-size: 30px;
  font-weight: 700;
  color: #f5f8ff;
}

.cs-hero__stat p {
  margin: 0;
  color: rgba(234, 240, 255, 0.75);
  font-size: 13px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  line-height: 1.35;
  word-break: break-word;
  display: grid;
  gap: 2px;
}

.cs-hero__stat p span {
  display: block;
}

.cs-trust {
  padding: 80px 0;
  background: linear-gradient(160deg, rgba(0, 61, 150, 0.18), rgba(0, 61, 150, 0));
}

.cs-trust__title {
  text-transform: uppercase;
  letter-spacing: 0.22em;
  font-size: 12px;
  color: var(--cs-muted);
  text-align: center;
  margin-bottom: 28px;
  max-width: 680px;
  margin-left: auto;
  margin-right: auto;
}

.cs-trust__columns {
  display: flex;
  justify-content: center;
  align-items: stretch;
  gap: clamp(32px, 6vw, 72px);
  flex-wrap: wrap;
}

.cs-trust__column {
  display: grid;
  gap: 18px;
  justify-items: center;
}

.cs-trust__group-title {
  font-size: 14px;
  letter-spacing: 0.14em;
  text-transform: uppercase;
  color: rgba(234, 240, 255, 0.75);
}

.cs-trust__divider {
  width: 1px;
  min-height: 140px;
  background: linear-gradient(180deg, rgba(0, 170, 255, 0), rgba(0, 170, 255, 0.45), rgba(0, 170, 255, 0));
  border-radius: 999px;
}

.cs-trust__logos {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: clamp(24px, 6vw, 60px);
  flex-wrap: wrap;
}

.cs-trust__logo {
  position: relative;
  background: transparent;
  border: 1px solid rgba(159, 178, 217, 0.14);
  border-radius: 16px;
  padding: 14px 22px;
  backdrop-filter: blur(8px);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  cursor: pointer;
  transition: transform 0.3s ease, border-color 0.3s ease, box-shadow 0.3s ease;
  filter: grayscale(100%) opacity(0.65);
}

.cs-trust__logo img {
  height: 32px;
  width: auto;
}

.cs-trust__logo span {
  position: absolute;
  bottom: -36px;
  left: 50%;
  transform: translateX(-50%);
  padding: 6px 12px;
  border-radius: 999px;
  background: rgba(8, 12, 24, 0.92);
  border: 1px solid rgba(0, 170, 255, 0.25);
  color: rgba(234, 240, 255, 0.92);
  font-size: 12px;
  letter-spacing: 0.08em;
  white-space: nowrap;
  opacity: 0;
  transform-origin: top;
  transition: opacity 0.25s ease, transform 0.25s ease;
  pointer-events: none;
}

.cs-trust__logo:hover {
  transform: translateY(-6px);
  border-color: rgba(0, 170, 255, 0.55);
  box-shadow: 0 24px 50px rgba(0, 170, 255, 0.2);
  filter: grayscale(0%) opacity(1);
}

.cs-trust__logo:hover span {
  opacity: 1;
  transform: translateX(-50%) translateY(-4px);
}

.cs-section {
  padding: 110px 0;
  position: relative;
  overflow: hidden;
}

.cs-section::before {
  content: '';
  position: absolute;
  inset: -20% -10% 40% -10%;
  background: radial-gradient(circle at top, rgba(0, 61, 150, 0.22), transparent 70%);
  opacity: 0.6;
  pointer-events: none;
  z-index: 0;
}

.cs-section__header {
  max-width: 620px;
  margin: 0 auto 48px;
  text-align: center;
  display: grid;
  gap: 14px;
}

.cs-section__header h2 {
  font-family: 'Poppins', sans-serif;
  font-size: clamp(32px, 4vw, 48px);
  margin: 0;
}

.cs-section__header p {
  color: var(--cs-muted);
}

.cs-eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.28em;
  font-size: 12px;
  color: var(--cs-muted);
}

.cs-carousel {
  overflow: hidden;
  position: relative;
}

.cs-carousel__track {
  display: flex;
  gap: 40px;
  align-items: stretch;
  justify-content: center;
  padding-bottom: 12px;
}

.cs-carousel__slide {
  flex: 0 0 320px;
  display: flex;
}

.cs-consultant-card {
  position: relative;
  background: linear-gradient(150deg, rgba(16, 23, 43, 0.9), rgba(0, 61, 150, 0.35));
  border-radius: var(--cs-radius);
  border: 1px solid rgba(0, 170, 255, 0.18);
  padding: 34px 28px;
  box-shadow: var(--cs-shadow-card);
  display: grid;
  justify-items: center;
  gap: 14px;
  transition: transform 0.4s ease, box-shadow 0.4s ease, border-color 0.4s ease;
  height: 100%;
  overflow: hidden;
}

.cs-consultant-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 38px 80px rgba(0, 170, 255, 0.24);
  border-color: rgba(0, 170, 255, 0.45);
}

.cs-consultant-card::before {
  content: '';
  position: absolute;
  inset: -50% 20%;
  background: radial-gradient(circle, rgba(0, 170, 255, 0.45), transparent 65%);
  opacity: 0;
  transition: opacity 0.4s ease;
  pointer-events: none;
}

.cs-consultant-card:hover::before {
  opacity: 1;
}

.cs-consultant-card > * {
  position: relative;
  z-index: 1;
}

.cs-consultant-card__avatar {
  width: 100px;
  height: 100px;
  display: grid;
  place-items: center;
  border-radius: 50%;
  padding: 4px;
  background: linear-gradient(140deg, rgba(0, 170, 255, 0.8), rgba(255, 145, 77, 0.6));
}

.cs-consultant-card__avatar img {
  width: 92px;
  height: 92px;
  border-radius: 50%;
  border: 3px solid rgba(12, 19, 35, 0.8);
  object-fit: cover;
}

.cs-consultant-card__role {
  color: var(--cs-muted);
  margin: 0;
}

.cs-consultant-card__rating {
  display: flex;
  align-items: center;
  gap: 10px;
}

.cs-stars {
  display: inline-flex;
  gap: 4px;
  color: #ffd166;
}

.cs-stars svg {
  width: 16px;
  height: 16px;
  transition: color 0.3s ease, filter 0.3s ease;
}

.cs-consultant-card__quote {
  text-align: center;
  color: var(--cs-muted);
  font-style: italic;
  min-height: 48px;
}

.cs-consultant-card__rate {
  font-weight: 600;
}

.cs-consultant-card__meta {
  margin: 0;
  color: rgba(234, 240, 255, 0.65);
  font-size: 13px;
}

.cs-consultant-card:hover .cs-stars {
  color: #ffd700;
  filter: drop-shadow(0 0 8px rgba(255, 215, 0, 0.6));
}

.cs-benefits__container {
  display: grid;
  gap: 48px;
}

.cs-benefits__grid,
.cs-process__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: clamp(24px, 4vw, 36px);
  perspective: 1000px;
}

.cs-post-project {
  background: linear-gradient(180deg, rgba(0, 61, 150, 0.12), rgba(11, 16, 32, 0.92));
}

.cs-post-project__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: clamp(32px, 6vw, 64px);
  align-items: start;
}

.cs-post-project__intro {
  display: grid;
  gap: 18px;
}

.cs-post-project__intro ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 10px;
  color: rgba(234, 240, 255, 0.75);
}

.cs-post-project__intro ul li {
  display: flex;
  gap: 10px;
  align-items: flex-start;
}

.cs-post-project__intro ul li::before {
  content: '';
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin-top: 7px;
  background: rgba(0, 170, 255, 0.85);
}

.cs-post-project__form {
  background: rgba(10, 16, 32, 0.92);
  border-radius: var(--cs-radius);
  border: 1px solid rgba(0, 170, 255, 0.18);
  padding: clamp(24px, 5vw, 36px);
  display: grid;
  gap: 18px;
  box-shadow: 0 28px 60px rgba(0, 61, 150, 0.28);
  backdrop-filter: blur(12px);
}

.cs-post-project__form label {
  display: grid;
  gap: 8px;
  font-size: 13px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: rgba(234, 240, 255, 0.68);
}

.cs-post-project__form input,
.cs-post-project__form select,
.cs-post-project__form textarea {
  background: rgba(234, 240, 255, 0.05);
  border: 1px solid rgba(0, 170, 255, 0.18);
  border-radius: 14px;
  padding: 12px 16px;
  color: var(--cs-text);
  font-size: 15px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease, transform 0.3s ease;
}

.cs-post-project__form select {
  appearance: none;
  background-image: linear-gradient(45deg, transparent 50%, rgba(234, 240, 255, 0.8) 50%), linear-gradient(135deg, rgba(234, 240, 255, 0.8) 50%, transparent 50%);
  background-position: calc(100% - 18px) calc(50% - 6px), calc(100% - 12px) calc(50% - 6px);
  background-size: 6px 6px;
  background-repeat: no-repeat;
}

.cs-post-project__form input:focus,
.cs-post-project__form select:focus,
.cs-post-project__form textarea:focus {
  outline: none;
  border-color: rgba(0, 170, 255, 0.55);
  box-shadow: 0 18px 28px rgba(0, 170, 255, 0.22);
}

.cs-post-project__file input {
  padding: 10px;
}

.cs-post-project__form button {
  justify-self: start;
  padding-inline: 28px;
  margin-top: 6px;
}

.cs-benefit-card,
.cs-process__card {
  background: linear-gradient(130deg, rgba(16, 23, 43, 0.85), rgba(0, 61, 150, 0.25));
  backdrop-filter: blur(18px);
  border-radius: var(--cs-radius-sm);
  border: 1px solid rgba(159, 178, 217, 0.18);
  padding: 32px 28px;
  display: grid;
  gap: 18px;
  position: relative;
  overflow: hidden;
  transition: transform 0.4s ease, border-color 0.4s ease, box-shadow 0.4s ease;
  transform-style: preserve-3d;
}

.cs-benefit-card::after,
.cs-process__card::after {
  content: '';
  position: absolute;
  inset: -40% -10%;
  background: radial-gradient(circle, rgba(0, 61, 150, 0.45), transparent 60%);
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
}

.cs-benefit-card:hover,
.cs-process__card:hover {
  transform: translateY(-10px) rotateX(2deg) rotateY(-2deg);
  border-color: rgba(0, 61, 150, 0.6);
  box-shadow: 0 36px 80px rgba(0, 61, 150, 0.22);
}

.cs-benefit-card:hover::after,
.cs-process__card:hover::after {
  opacity: 1;
}

.cs-benefit-card__icon,
.cs-process__icon {
  font-size: 32px;
  color: var(--cs-orange);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;
  border-radius: 16px;
  background: rgba(255, 145, 77, 0.12);
  box-shadow: 0 12px 28px rgba(255, 145, 77, 0.25);
  animation: cs-icon-float 6s ease-in-out infinite;
}

.cs-process__icon {
  color: var(--cs-blue);
  background: rgba(0, 61, 150, 0.18);
  box-shadow: 0 12px 28px rgba(0, 61, 150, 0.25);
}

.cs-benefit-card ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 0;
  padding: 0;
}

.cs-benefit-card ul li {
  font-size: 13px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  padding: 6px 12px;
  border-radius: 999px;
  background: rgba(234, 240, 255, 0.08);
  border: 1px solid rgba(159, 178, 217, 0.16);
  color: rgba(234, 240, 255, 0.75);
}

.cs-process {
  background: linear-gradient(180deg, rgba(0, 61, 150, 0.08), transparent);
}

.cs-testimonial {
  position: relative;
}

.cs-testimonial::before {
  content: '';
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at top left, rgba(0, 61, 150, 0.25), transparent 60%);
  z-index: -1;
}

.cs-testimonial__wrapper {
  display: grid;
  gap: 48px;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  align-items: center;
}

.cs-testimonial__quote {
  background: var(--cs-card);
  border-radius: var(--cs-radius);
  border: 1px solid var(--cs-card-border);
  padding: clamp(32px, 5vw, 48px);
  box-shadow: var(--cs-shadow-card);
  display: grid;
  gap: 22px;
}

.cs-testimonial__quote blockquote {
  margin: 0;
  font-size: clamp(22px, 4vw, 32px);
  line-height: 1.4;
}

.cs-testimonial__author {
  display: flex;
  align-items: center;
  gap: 16px;
}

.cs-testimonial__author img {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  object-fit: cover;
}

.cs-testimonial__author span {
  color: var(--cs-muted);
  font-size: 14px;
}

.cs-testimonial__metrics {
  display: grid;
  gap: 22px;
}

.cs-testimonial__metrics div {
  background: rgba(12, 19, 35, 0.85);
  border-radius: var(--cs-radius-sm);
  padding: 28px;
  border: 1px solid rgba(0, 61, 150, 0.28);
  box-shadow: 0 20px 55px rgba(0, 61, 150, 0.18);
}

.cs-testimonial__metrics strong {
  font-size: 28px;
}

.cs-testimonial__metrics span {
  color: var(--cs-muted);
}

.cs-contact {
  background: linear-gradient(180deg, rgba(16, 23, 43, 0.85), rgba(11, 16, 32, 0.96));
}

.cs-contact__layout {
  display: grid;
  gap: clamp(32px, 6vw, 64px);
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  align-items: center;
}

.cs-contact__intro {
  display: grid;
  gap: 18px;
}

.cs-contact__cta {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}

.cs-contact__meta {
  color: var(--cs-muted);
}

.cs-contact__card {
  background: rgba(8, 12, 26, 0.9);
  border-radius: var(--cs-radius);
  border: 1px solid rgba(159, 178, 217, 0.14);
  padding: clamp(24px, 4vw, 32px);
  display: grid;
  gap: 24px;
  box-shadow: var(--cs-shadow-card);
}

.cs-contact__form {
  display: grid;
  gap: 18px;
}

.cs-contact__form label {
  display: grid;
  gap: 8px;
  font-size: 14px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: var(--cs-muted);
}

.cs-contact__form input,
.cs-contact__form textarea {
  background: rgba(234, 240, 255, 0.06);
  border: 1px solid rgba(159, 178, 217, 0.18);
  color: var(--cs-text);
  border-radius: 14px;
  padding: 12px 16px;
  font-size: 15px;
}

.cs-contact__form input:focus,
.cs-contact__form textarea:focus {
  outline: none;
  border-color: rgba(0, 61, 150, 0.7);
  box-shadow: 0 0 0 3px rgba(0, 61, 150, 0.25);
}

.cs-contact__info {
  display: grid;
  gap: 10px;
  font-size: 15px;
  color: var(--cs-muted);
}

.cs-footer {
  background: rgba(8, 12, 26, 0.92);
  padding: 72px 0 40px;
  border-top: 1px solid rgba(159, 178, 217, 0.08);
}

.cs-footer__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 32px;
  margin-bottom: 48px;
}

.cs-footer__grid h4 {
  font-family: 'Poppins', sans-serif;
  margin-bottom: 16px;
}

.cs-footer__grid h5 {
  margin-bottom: 12px;
}

.cs-footer__grid a {
  display: block;
  color: var(--cs-muted);
  margin-bottom: 8px;
}

.cs-footer__grid a:hover {
  color: var(--cs-text);
}

.cs-footer__bottom {
  border-top: 1px solid rgba(159, 178, 217, 0.08);
  padding-top: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 16px;
  font-size: 14px;
  color: var(--cs-muted);
}

.cs-footer__social {
  display: inline-flex;
  gap: 14px;
}

.cs-footer__social a {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: grid;
  place-items: center;
  background: rgba(234, 240, 255, 0.05);
  border: 1px solid rgba(159, 178, 217, 0.18);
  transition: background 0.3s ease, transform 0.3s ease;
}

.cs-footer__social a:hover {
  background: rgba(0, 61, 150, 0.4);
  transform: translateY(-3px);
}

@keyframes cs-icon-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-6px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes cs-button-ripple {
  0% {
    transform: scale(0.95);
    opacity: 0.7;
  }
  70% {
    transform: scale(1.05);
    opacity: 0.15;
  }
  100% {
    transform: scale(1.1);
    opacity: 0;
  }
}

@media (max-width: 1024px) {
  .cs-navbar__links,
  .cs-navbar__cta {
    display: none;
  }

  .cs-navbar__divider {
    display: none;
  }

  .cs-navbar__menu {
    display: block;
  }

  .cs-hero__content {
    grid-template-columns: 1fr;
    gap: 38px;
  }

  .cs-hero__stats {
    justify-self: stretch;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    width: 100%;
  }

  .cs-trust__columns {
    gap: 40px;
  }
}

@media (max-width: 768px) {
  .cs-navbar {
    padding: 14px 0;
  }
  .cs-navbar__brand img {
    height: 44px;
  }

  .cs-section {
    padding: 80px 0;
  }

  .cs-hero {
    padding-top: 120px;
  }

  .cs-hero__actions {
    flex-wrap: wrap;
  }

  .cs-hero__search-tags {
    flex-wrap: wrap;
  }

  .cs-carousel__slide {
    flex-basis: 280px;
  }

  .cs-contact__cta {
    flex-direction: column;
    align-items: flex-start;
  }

  .cs-post-project__grid {
    gap: 32px;
  }

  .cs-post-project__form button {
    width: 100%;
    justify-content: center;
  }

  .cs-trust__columns {
    gap: 28px;
  }

  .cs-trust__divider {
    display: none;
  }
}

@media (max-width: 640px) {
  .cs-hero__stats {
    grid-template-columns: 1fr;
  }

  .cs-hero__search {
    max-width: 100%;
  }
}
